<template>
    <div class="container">
        <img src="../../assets/logo.png" class="img-fluid w-25 p-3" alt="">
        <div class="row">
            <!-- <div class="col">
                <div class="card card-winner">
                    <div class="card-header">
                        <h1>Constestants:</h1>
                    </div>
                    <div class="card-body">
                        <div class="form-check">
                            <input v-model="selected" class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios1" value="100" checked>
                            <label class="form-check-label" for="exampleRadios1">
                                100
                            </label>
                        </div>
                        <div class="form-check">
                            <input v-model="selected" class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios2" value="50">
                            <label class="form-check-label" for="exampleRadios2">
                                50
                            </label>
                        </div>
                        <div class="form-check">
                            <input v-model="selected" class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios3" value="25">
                            <label class="form-check-label" for="exampleRadios3">
                                25
                            </label>
                        </div>
                    </div>
                    Selected:{{selected}}
                </div>
            </div> -->
            <div class="col">

                <div class="card card-winner">
                    <div class="card-header">
                            <h1> We have a Winner!! </h1>
                    </div>
                    <div class="card-body">
                        
                        <h1 class="text-success" v-show="winnerSelected">
                            <span class="badge badge-success">{{rndmNumber}}</span>{{winner.Name}}
                        </h1>
                        <h3 class="" v-show="winnerSelected">
                            Mail: {{winner.Email}}
                        </h3>
                        <h3 class="" v-show="winnerSelected">
                            Company: {{winner.Company}}
                        </h3>

                        <h2 class="text-muted" v-show="!winnerSelected">Please press the button to select a Winner</h2>
                    </div>
                </div>
                <div class="row">
                    <div class="col">
                        <button @click="getRandomNumber" class=" w-25 p-3 random-btn btn btn-lg btn-primary">Get Winner</button>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
    export default {
        name: 'randomizer',
        data() {
            return {
                rndmNumber: 0,
                constestants: [],
                winner: {},
                selected: 100,
                winnerSelected: false
            }
        },
        computed: {

        },
        methods: {
            getRandomNumber() {

                this.rndmNumber = Math.floor(Math.random() * (this.constestants.length - 1 + 1)) + 1;
                //this.rndmNumber = Math.floor(Math.random() * (this.selected - 1 + 1)) + 1;
                this.getContestant(this.rndmNumber);
                this.winnerSelected = true;
            },
            getContestant(index) {
                this.winner = this.constestants[index - 1];
            },
            getAllContestants() {
                axios.get('http://sxlottery-api.azurewebsites.net/api/Person')
                    .then((response) => {
                        this.constestants = response.data;
                    })
                    .catch((error) => {
                        console.log(error);
                    });
            }
        },
        mounted() {
            this.getAllContestants();
        }
    }
</script>

<style>
    .badge-success {
        font-size: 1.5em;
    }


    .random-btn,
    .card-winner {
        margin-top: 1em;
    }
</style>


